<template>
  <div>
    <Header></Header>
    <main class="main dashboard-main">
      <div class="container-fluid" style="padding-top: 30px">
        <div class="animated fadeIn">
          <div class="content-header"></div>
          <!--content-header-->
          <div class="row">
            <div class="col">
              <div class="">
                <h4
                  style="margin-left: 20px; font-size: 30px; font-weight: bold"
                >
                </h4>
                <!--card-header-->
                <div class="card-body">
                  <div class="row">
                    <div
                      class="col-md-3"
                      v-for="item in Cursos"
                      :key="item.ID"
                      @click.prevent="goTO('/course-view/' + item.COURSE_ID)"
                    >
                      <div
                        class="best-course-pic-text position-relative border"
                      >
                        <div
                          class="
                            best-course-pic
                            position-relative
                            overflow-hidden
                          "
                          :style="[
                            getURL(item.photo)
                              ? {
                                  background: 'url(' + getURL(item.photo) + ')',
                                }
                              : {
                                  backgroundImage:
                                    'url(' +
                                    require('@/assets/images/1608244179-1.png') +
                                    ')',
                                },
                          ]"
                        >
                          <div class="course-rate ul-li">
                            <ul>
                              <li><i class="fas fa-star"></i></li>
                              <li><i class="fas fa-star"></i></li>
                              <li><i class="fas fa-star"></i></li>
                              <li><i class="fas fa-star"></i></li>
                              <li><i class="fas fa-star"></i></li>
                            </ul>
                          </div>
                        </div>
                        <div class="best-course-text d-inline-block w-100 p-2">
                          <div
                            class="course-title mb20 headline relative-position"
                          >
                            <h5>
                              <a href="#">{{ item.NAME }}</a>
                            </h5>
                          </div>
                          <div class="course-meta d-inline-block w-100">
                            <div class="d-inline-block w-100 0 mt-2">
                              <p>
                                {{ getDescription(item.DESCRIPTION) }}
                              </p>
                            </div>
                            <div class="progress my-2">
                              <div
                                class="progress-bar concluido"
                                v-if="item.percentage > 99"
                                style="width: 100%"
                              >
                                Concluído 100 %
                              </div>
                              <div
                                v-else
                                class="progress-bar andamento"
                                style="width: 100%"
                              >
                                Em andamento {{ getPercent(item.percentage) }} %
                               
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>
<script>
// @ is an alias to /src
import { reactive, ref } from "vue";
import Header from "../../components/Header/index.vue";
import Footer from "../../components/Footer/index.vue";
import Routes from "../../services/Routes";
import * as moment from "moment";
import Swal from "sweetalert2";
import AOS from "aos";

export default {
  name: "Home",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      menu: true,
      Cursos: false,
    };
  },
  methods: {
    getPercent(n) {
      let val = (n / 1).toFixed(2).replace(".", ",");
      return "R$ " + val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    },
    getDescription: function (name) {
      if (name.length > 250) {
        return name.substring(0, 250) + "...";
      }
      return name;
    },
    getURL(item) {
      var a = "";
      for (var obj in item) {
        a = item[obj];
        if (a.includes("https", 0) == true) {
          return a;
        } else {
          return null;
        }
      }
    },
    goTO(url) {
      this.$router.push(url);
    },
    getCourses() {
      let data = {
        USER_ACCOUNT_ID: JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          ? JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          : 0,
        USER_ID: JSON.parse(localStorage.getItem("userID")),
      };
      Routes.getCourses(data)
        .then((r) => {
          this.Cursos = r.data.DATA;
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.getCourses();
    AOS.init();
  },

  created() {},
  setup() {},
};
</script>

<style scoped>
.app-header .navbar-toggler {
  display: none;
}

.best-course-pic-text .best-course-pic {
  width: 100% !important;
  height: 250px !important;
}

.best-course-pic-text {
  border-radius: 4px;
  padding-top: 0px;
}

.best-course-pic-text {
  cursor: pointer;
}

.app-header .navbar-brand {
  width: 150px;
  padding: 5px 10px;
}
.concluido {
  width: 100%;
  background-color: #017803;
}
</style>
